import React, { useState } from 'react'

import Layout from '../layout'
import Container from '../container'

import styles from './index.module.css'

const AdminNotAuthenitcated = ({checkPassword}) => {

  const [ password, setPassword ] = useState('')

  const handlePassword = (e) => {
    e.preventDefault()

    checkPassword(password)
  }

  const handleInput = (e) => {

    setPassword(e)
  }

  return (
    <Layout>
    <Container title="Please enter email">
    <form onSubmit={handlePassword}>
      <label>
        Password:
        <input type="password" name="adminPassword" onChange={e => handleInput(e.target.value)} value={password} className={styles.passwordInput}/>
      </label>
      <input type="submit" value="Submit" className={styles.passwordSubmit}/>
    </form>
    </Container>
    </Layout>
  )
}

export default AdminNotAuthenitcated