import axios from 'axios'
import {navigate} from 'gatsby'

let config = {
  headers: {
    'Content-Type': 'application/json',
    'Cache-Control': 'no-cache',
  }
}


export const getUsers = () => {
  return axios.get(process.env.SERVER_URL_GET_USERS)
    .then(res => res)
    .catch(err => navigate('/404', err))
}

export const updateUserPayment = (data) => {
  axios.post(process.env.SERVER_URL_UPDATE_USER_PAYMENT, data, config)
    .then(res=> res)
    .catch(err => console.log('User not updated', err.response))
  }