import React from 'react'

import {
  useTable, useSortBy
} from 'react-table'

import { updateUser } from './userData'
import styles from './index.module.css'

import DatePickerComponent from '../date-picker'

const EditableCell = ({
  cell: { value: initialValue },
  row: { index },
  column: { id },
  cell,
  updateUser, // This is a custom function that we supplied to our table instance
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)

  const onChange = e => {
    setValue(e.target.value)
  }

  // We'll only update the external data when the input is blurred
  const onBlur = () => {
    updateUser({
      id: cell.row.original.id,
      [id]: value
    })
  }

  // If the initialValue is changed externall, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  return <input value={value || ''} onChange={onChange} onBlur={e => onBlur(e)} className={styles.inputCell}/>
}

const RegularCell = ({
  cell: { value: initialValue },
  row: { index },
  column: { id },
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)

  // If the initialValue is changed externall, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  return <p className={styles.regularCell}>{value}</p>
}

const DateCell = ({
  cell: { value: initialValue },
  row: { index },
  column: { id },
  cell
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)

  // If the initialValue is changed externall, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  const onDateSelect = (selectedDate) => {
    updateUser({
      id: cell.row.original.id,
      [id]: selectedDate
    })
  }

  return <DatePickerComponent selectedDate={initialValue} updatePaymentDate={onDateSelect} />
}

const DropdownCell = ({
  cell: { value: initialValue },
  row: { index },
  column: { id },
  cell,
  }) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)
  // If the initialValue is changed externall, sync it up with our state
  React.useEffect(() => {
    setValue(value)
  }, [value])

  const onDropdownSelect = (val) => {
    updateUser({
      id: cell.row.original.id,
      [id]: val
    })

    setValue(val)
  }

  return (
    <select onChange={e => onDropdownSelect(e.target.value)} value={value || ''} className={styles.dropdownCell}>
      {
        cell.column.dropdownvalues.map(cellValue => (
          <option value={cellValue.value} key={cellValue.value}>{cellValue.label}</option>
        ))
      }
    </select>
  )
}

// Set our editable cell renderer as the default Cell renderer
const defaultColumn = {
  Cell: EditableCell,
  minWidth: 20,
  width: 50,
  maxWidth: 100,
}

function Table({ data }) {
  // Use the state and functions returned from useTable to build your UI
  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        columns: [
          {
            Header: 'Name',
            accessor: 'name',
            Cell: RegularCell,
            width: 30,
            minWidth: 30
          },
          {
            Header: 'Email',
            accessor: 'email',
            Cell: RegularCell,
            width: 200,
            minWidth: 200
          }
        ],
      },
      {
        Header: 'Info',
        columns: [
          {
            accessor: "reference_code",
            Header: "Ref Code",
            Cell: RegularCell,
            width: 20,
            minWidth: 30
          },
          {
            accessor: "status",
            Header: "status",
            Cell: DropdownCell,
            dropdownvalues: [{
              label: 'Paid',
              value: 'paid'
            },
            {
              label: 'Unpaid',
              value: 'unpaid'
            }
            ]
          },
          {
            accessor: "form_of_payment",
            Header: "Pay Method",
            Cell: DropdownCell,
            dropdownvalues: [
              {
                label: 'None',
                value: 'none'
              },
              {
                label: 'Payme',
                value: 'payme'
              },
              {
                label: 'Bank',
                'value': 'bank'
              },
              {
                label: 'Cash',
                'value': 'cash'
              },
              {
                label: 'Other',
                value: 'other'
              }
            ]
          },
          {
            accessor: "paid_at",
            Header: "Paid At",
            Cell: DateCell
          },
          {
            accessor: "note",
            Header: "Note",
          },
        ],
      },
    ], []
  )

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    rows
  } = useTable({
    columns,
    data,
    defaultColumn,
    updateUser,
  }, useSortBy)

  // Render the UI for your table
  return (
    <>
      <table {...getTableProps()} className={styles.adminTable}>
        <thead className={styles.adminTableHead}>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()} className={styles.adminTableRow}>
              {headerGroup.headers.map(column => (
                <th {...column.getHeaderProps(column.getSortByToggleProps())} className={styles.adminTableHeaderColumns}><u>{column.render('Header')}</u>
                  <span>
                    {column.isSorted ? (column.isSortedDesc ? ' 🔽' : ' 🔼') : ''}
                  </span>
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row, i) => {
            prepareRow(row)
            return (
              <tr {...row.getRowProps()} className={`${styles.adminTableRow} ${row.values.status === 'unpaid' ? styles.unPaidRow : ''}`}>
                {row.cells.map(cell => {
                  return <td {...cell.getCellProps()} className={styles.adminTableData}>{cell.render('Cell')}</td>
                })}
              </tr>
            )
          })}
        </tbody>
      </table>
    </>
  )
}



export default Table