import React, { useState } from "react";
import DatePicker from "react-datepicker";
import { format, parseISO } from 'date-fns'

import "react-datepicker/dist/react-datepicker.css";

// CSS Modules, react-datepicker-cssmodules.css
import 'react-datepicker/dist/react-datepicker-cssmodules.css';

const DatePickerComponent = ({selectedDate, updatePaymentDate}) => {
  selectedDate = selectedDate ? parseISO(selectedDate) : null
  const [startDate, setStartDate] = useState(selectedDate)

  const handleChange = (newDate) => {
    setStartDate(newDate)

    updatePaymentDate(format(newDate, 'yyyy-MM-dd'))
  }

  return (
    <DatePicker selected={startDate} onChange={date => handleChange(date)} />
  );
}

export default DatePickerComponent